import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import * as echarts from 'echarts/lib/echarts';
declare var require: any;
require('echarts/lib/chart/bar');
require("echarts/lib/chart/line");
require('echarts/lib/component/tooltip');
require('echarts/lib/component/title');

@Component({
  selector: 'page-grade-analytics',
  templateUrl: 'grade-analytics.html',
})
export class GradeAnalyticsPage {

  constructor(public navCtrl: NavController, public navParams: NavParams) {
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad GradeAnalyticsPage');
  }
  ionViewDidEnter() {
    const ec = echarts as any;
    var myChart = ec.init(document.getElementById('chart'));
    var optionchart = {
      title: {
        text: '语文近六次成绩',
        left:"center",
        top:"8%"
      },
      tooltip: {},
      xAxis: {
        type: 'category',
        data: ['1-31', '2-26', '3-25', '4-30', '5-25', '6-28']
      },
      yAxis: {
        type: 'value',
        data:[0,50,100,150]
      },
      series: [{
        data: [80, 90, 120, 106, 130, 135],
        type: 'line'
      }]
    }
    myChart.setOption(optionchart);
  }

}



