import { Component } from '@angular/core';
import { Platform } from 'ionic-angular';
import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';

import { HomePage } from '../pages/home/home';
import { RecommendPage } from '../pages/recommend/recommend';
import { DiscoverPage } from '../pages/discover/discover';
import { NewsPage } from '../pages/news/news';
import { PersonPage } from '../pages/person/person';

import { HttpService } from '../services/http.service';

@Component({
    templateUrl: 'app.html'
})
export class MyApp {
    userType: string = "parent";
    home: any;
    compass: any;
    news: any;
    person: any;

    constructor(platform: Platform, statusBar: StatusBar, splashScreen: SplashScreen, httpService: HttpService) {
        platform.ready().then(() => {
            // Okay, so the platform is ready and our plugins are available.
            // Here you can do any higher level native things you might need.
            statusBar.styleDefault();
            splashScreen.hide();
            this.setFontSize();
        });
        this.home = this.userType == "parent" ? HomePage : RecommendPage;
        this.compass = this.userType == "parent" ? RecommendPage : DiscoverPage;
        this.news = NewsPage;
        this.person = PersonPage;
    }
    
    setFontSize() {
        let root = document.documentElement;
        let rem = root.clientWidth / 375 * 10;
        rem = (rem >= 16) ? 16 : rem;
        root.style.fontSize = rem + "px";
    }
}




