import { Component } from '@angular/core';
import { DomSanitizer } from '@angular/platform-browser';
import { NavController } from 'ionic-angular';

import { DetailsPage } from '../details/details';
import { HttpService } from '../../services/http.service';

@Component({
    selector: 'page-recommend',
    templateUrl: 'recommend.html'
})
export class RecommendPage {
    private blogs: any = [];
    private nav = "care";
    
    constructor(
        public sanitizer: DomSanitizer,
        public navCtrl: NavController,
        public httpService: HttpService
    ) { }
    
    ionViewDidLoad() {
        this.httpService.httpRequest('getBlogListService').then((res) => {
            if(res.status == "success") {
                this.blogs = res.resultInfo;
                for(let i = 0;i < this.blogs.length; i++) {
                    for(let j = 0;j < this.blogs[i].pics.length;j++) {
                        this.blogs[i].pics[j].url = this.sanitizer.bypassSecurityTrustStyle('url(' + this.blogs[i].pics[j].url + ')');
                    }
                }
            }
        });
    }

    goToDetails(id) {
        this.navCtrl.push(DetailsPage);
    }
    
    changeChannel(channel) {
        this.nav = channel;
    }
}



